"use strict";

import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import AboutView from "@/views/AboutView.vue";
import BlogView from "@/views/BlogView.vue";
import ContactView from "@/views/ContactView.vue";
import HomeView from "@/views/HomeView.vue";
import AllSoftwareView from "@/views/AllSoftwareView.vue";
import BedfordLake from "@/views/BedfordLakeSoftwareView.vue";

interface RouteMetaMetaElement {
  content?: string,
  name: string
}

declare module "vue-router" {
  interface RouteMeta {
    title: string,
    metaElements: Array<RouteMetaMetaElement>
  }
}

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "Home",
    component: HomeView,
    meta: {
      title: "Home",
      metaElements: [
        {
          name: "description",
          content: "Smoky Peak Software is a new software company based in Maryville, Tennessee."
        },
        {
          name: "og:description",
          content: "Smoky Peak Software is a new software company based in Maryville, Tennessee."
        }
      ]
    }
  },

  {
    path: "/about",
    name: "About",
    component: AboutView,
    meta: {
      title: "About",
      metaElements: [
        {
          name: "description",
          content: "Find out more about Smoky Peak Software's history and founders."
        },
        {
          name: "og:description",
          content: "Find out more about Smoky Peak Software's history and founders."
        }
      ]
    }
  },

  {
    path: "/blog",
    name: "Blog",
    component: BlogView,
    meta: {
      title: "Blog",
      metaElements: [
        {
          name: "description",
          content: "Read what's going on at Smoky Peak Software."
        },
        {
          name: "og:description",
          content: "Read what's going on at Smoky Peak Software."
        }
      ]
    }
  },

  {
    path: "/contact",
    name: "Contact",
    component: ContactView,
    meta: {
      title: "Contact",
      metaElements: [
        {
          name: "description",
          content: "Contact Smoky Peak Software."
        },
        {
          name: "og:description",
          content: "Contact Smoky Peak Software."
        }
      ]
    }
  },

  {
    path: "/software",
    name: "Software",
    component: AllSoftwareView,
    meta: {
      title: "Software",
      metaElements: [
        {
          name: "description",
          content: "Visit Smoky Peak Software's software page for a catalog of our applications."
        },
        {
          name: "og:description",
          content: "Visit Smoky Peak Software's software page for a catalog of our applications."
        }
      ]
    }
  },

  {
    path: "/software/bedford-lake",
    name: "Bedford Lake",
    component: BedfordLake,
    meta: {
      title: "Bedford Lake",
      metaElements: [
        {
          name: "description",
          content: "Bedford Lake is the codename of Smoky Peak Software's flagship application. Check back often for updates and a release date."
        },
        {
          name: "og:description",
          content: "Bedford Lake is the codename of Smoky Peak Software's flagship application. Check back often for updates and a release date."
        }
      ]
    }
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

router.beforeEach((to, from, next) => {
  const title = "Smoky Peak Software";

  document.title = `${to.meta.title} · ${title}`;

  // Remove existing custom meta elements

  const existingMetaElements = document.querySelectorAll("[data-vue-router-controlled]");

  for (const element of existingMetaElements) {
    element.remove();
  }

  for (const metaElement of to.meta.metaElements) {
    const element: HTMLMetaElement = document.createElement("meta");

    element.name = metaElement.name;

    element.toggleAttribute("data-vue-router-controlled", true);

    if (metaElement.content) {
      element.setAttribute("content", metaElement.content);
    }

    document.head.appendChild(element);
  }

  next();
});

export default router;
